*{
    margin: 0;
    padding: 0;
}
article{
    width: 80%;
    margin: auto;
    margin-top: 5%;
}
.cart_box img{
    width: 100px;
    height: 100px;
    border-radius: 10px;
}
.cart_img{
    display: flex;
    width: 400px;
    align-items: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    
}
.cart_img p {
    font-weight: bold;
    margin-left: 10px;
    margin-left: 10%;
}
.cart_box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid rgb(0, 0, 0);
    padding-bottom: 30px;
    margin-top: 2%;
}
.cart_box div:nth-child(2) button{
    padding: 5px 10px;
    font-weight: bold;
    margin-right: 5px;
    border-radius: 2px;
}
.cart_box div:nth-child(3) button{
    padding: 5px 10px;
    font-weight: bold;
    letter-spacing: 2px;
    border-radius: 2px;
    font-size: 1.2vmax;
    border:none;
    outline: none;
    color: white;
    background-color: rgb(236, 8, 8);
    border-radius: 5px;
    cursor: pointer;
}

.cart_box div:nth-child(3) span{
    /* background-color: rgb(138, 164, 177); */
    background-color: black;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 10px;
}
.total{
    display: flex;
    justify-content: space-between;
    margin-top: 3%;
}
.total span:nth-child(1){
    font-size: 2rem;
    color: rgb(229, 13, 13);
    font-weight: bold;
    letter-spacing: 2px;
}
.total span:nth-child(2){
    font-size: 2rem;
    color: rgb(242, 0, 0);
    font-weight: bold;
}
.btn .dec{
    border-radius: 5px;
    background-color: black;
    color: white    ;
}
.btn .inc{
    border-radius: 5px;
    background-color: black;
    color: white    ;
}
.rate span{
    font-size: 1.3vmax;
    font-weight: 900;
}